import React, { useState, useEffect } from 'react'
import Navbar from '../Components/Navbar'
import { Link } from 'react-router-dom'
import { apiBaseUrl, mediaUrl } from '../Config'
import axios from 'axios'

export default function Orders() {

    const [orders, setOrders] = useState([])
    const status = ["pending", "Processing", "Completed", "Cancelled"]


    async function orderStauts(id, e) {
        let status = e.target.value
        let res=await axios.put(`${apiBaseUrl}orders/update-status/${id}`,{status})
        console.log(res)
    }


    useEffect(() => {
        async function getorders() {
            const res = await axios.get(`${apiBaseUrl}orders`)
            console.log(res)
            setOrders(res.data.msg)
        }

        getorders()
    }, [])

    

    return (
        <>
            <Navbar />
            <div className='container my-4'>
                <h1 className="text-center mb-4">Orders</h1>
                {
                    orders.length === 0 ?
                        <h1>No order found !</h1> :
                        <table className="table table-dark">
                            <thead>
                                <tr>
                                    <th>Order No</th>
                                    <th>Customer Name</th>
                                    <th>Customer Email</th>
                                    <th>order Id</th>
                                    <th>Price</th>
                                    <th>Quantity</th>
                                    <th>Address</th>
                                    <th>Stauts</th>
                                </tr>
                            </thead>
                            <tbody>
                                {
                                    orders.map((order, index) => {
                                        return (
                                            <tr key={index}>
                                                <th>{index + 1}</th>
                                                <td>{order.name}</td>
                                                <td>{order.email}</td>
                                                <td>{orders[index].product[0].pid}</td>
                                                <td>{orders[index].product[0].price}</td>
                                                <td>{orders[index].product[0].quantity}</td>
                                                <td>{order.address}</td>
                                                <td>
                                                    <select className='form-control' onChange={(e) => orderStauts(order._id, e)}>
                                                        {
                                                            status.map((status, index) => {
                                                                return (
                                                                    <option key={index} selected={order.status == status ? true : false}
                                                                        disabled={order.status == status ? true : false}
                                                                        value={status}>{status}</option>
                                                                )
                                                            })
                                                        }
                                                    </select>
                                                </td>
                                            </tr>
                                        )

                                    })
                                }
                            </tbody>
                        </table>
                }
            </div>
        </>
    )
}
