import axios from 'axios'
import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom'
import { apiBaseUrl } from '../Config'

export default function Adduser() {
    const [email, setEmail] = useState("")
    const [errMsg, setErrMsg] = useState('')
    const [password, setPassword] = useState('')
    const navigate = useNavigate()

    async function login(e) {
        e.preventDefault()
        if (!email || !password) {
            setErrMsg("Please Fill all the fields")
        } else {
            try {
                let res = await axios.post(`${apiBaseUrl}user/add`, { email, password })
                console.log(res)
                if (res.data.status === 200) {
                    let res2 = await axios.post(`${apiBaseUrl}user/login`, { email, password })
                    localStorage.setItem("email", res2.data.msg.email)
                    navigate('/dashboard')
                    setErrMsg('')
                }
            } catch (err) {
                console.log(err)
                setErrMsg('already registered user')
            }

        }
    }
    return (
        <>
            <div className="p-5 myjumbo text-center bg-primary text-white">
                <h1 className='p-5' style={{ textDecoration: "underline" }}>Admin Panel</h1>
                <h3>New user Registration !</h3>
            </div>
            <div className='container-fluid'>
                <div className='row'>
                    <div className='col sm-1'></div>
                    <div className='col sm-10'>
                        <form className='mt-4' onSubmit={login}>
                            <div className="mb-3">
                                <label className="form-label">Email address</label>
                                <input type="email" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" onChange={(e) => setEmail(e.target.value)} />
                            </div>
                            <div className="mb-3">
                                <label className="form-label">Password</label>
                                <input type="password" className="form-control" id="exampleInputPassword1" aria-describedby="passwordHelpBlock" onChange={(e) => setPassword(e.target.value)} />
                            </div>
                            <p style={{ color: "red" }}>{errMsg}</p>
                            <button type="submit" className="btn btn-primary">Add New user</button>
                        </form>
                        <a className=' active' style={{ "cursor": "pointer" }} onClick={(e) => {
                            e.preventDefault()
                            navigate('/')
                        }} >Already registered!</a>
                    </div>
                    <div className='col sm-1'></div>
                </div>
            </div>
        </>
    )
}
