import React from 'react'
import { Link } from 'react-router-dom'
import Navbar from '../Components/Navbar'
import AddPro from './AddPro'
import { useState, useEffect } from 'react'
import axios from 'axios'
import { apiBaseUrl, mediaUrl } from '../Config'


export default function Product() {
    const [product, setProduct] = useState([])

    useEffect(() => {
        async function fetchData() {
            let res = await axios.get(`${apiBaseUrl}product`)
            setProduct(res.data.msg)
        }
        fetchData()
    }, [])

    async function del(id) {
        let res = await axios.delete(`${apiBaseUrl}product/del/${id}`)
        if (res.data.status === 200) {
        alert('Deleted')
        window.location.reload()
        }
    }
    return (
        <>
            <Navbar />
            <div className='container-fluid'>
                <h1 className='text-center my-5'>Product</h1>
                <Link to='/addpro' className='btn btn-primary mb-3'>Add Product</Link>
                <table className="table">
                    <thead>
                        <tr>
                            <th >S.No</th>
                            <th >Name</th>
                            <th >Picture</th>
                            <th >Price</th>
                            <th >Description</th>
                            <th >Added By</th>
                            <th >Added On</th>
                            <th>Action</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            product.map((v, i) => {
                                return (
                                    <tr key={i}>
                                        <th >{i + 1}</th>
                                        <td>{v.name.toUpperCase()}</td>
                                        <td><img style={{ "height": "100px", "width": "150px" }} src={`${mediaUrl}product/${v.picture}`} /></td>
                                        <td>&#x20b9; {v.price} /-</td>
                                        <td>{v.description.slice(0, 15)}...</td>
                                        <td>{v.addedBy}</td>
                                        <td>{v.addedOn}</td>
                                        <td><button className='btn btn-danger' onClick={() => { del(v._id) }}>Delete</button></td>
                                    </tr>
                                )
                            })
                        }
                    </tbody>
                </table>
            </div>
        </>
    )
}
