import React, { useEffect, useState } from 'react'
import axios from 'axios'
import '../App.css'
import { apiBaseUrl } from '../Config'
import { useNavigate } from 'react-router-dom'



export default function Login() {

    // veriables

    const [email, setEmail] = useState("")
    const [password, setPassword] = useState("")
    const [errMsg, setErrMsg] = useState('')
    const navigate = useNavigate()


    useEffect(() => {
        var isUser = localStorage.getItem("email")
        if (isUser) {
            navigate('/dashboard')
        }
    },[])
    //already login ends!



    async function login(e) {
        e.preventDefault()
        if (!email || !password) {
            setErrMsg("Please fill all the fields")
        } else {
            try {
                let res = await axios.post(`${apiBaseUrl}user/login`, { email, password })
                console.log(res)
                localStorage.setItem("email", res.data.msg.email)
                navigate('/dashboard')
                setErrMsg("")
            } catch (err) {
                setErrMsg("Wrong Credentials")
            }

        }
    }

    return (
        <>
            <div className="p-5 myjumbo text-center bg-primary text-white">
                <h1 className='p-5' style={{ textDecoration: "underline" }}>Admin Panel</h1>
                <h3>Login Here !</h3>
            </div>
            <div className='container-fluid'>
                <div className='row'>
                    <div className='col sm-1'></div>
                    <div className='col sm-10'>
                        <form className='mt-4' onSubmit={login}>
                            <div className="mb-3">
                                <label className="form-label">Email address</label>
                                <input type="email" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" onChange={(e) => setEmail(e.target.value)} />
                            </div>
                            <div className="mb-3">
                                <label className="form-label">Password</label>
                                <input type="password" className="form-control" id="exampleInputPassword1" aria-describedby="passwordHelpBlock" onChange={(e) => setPassword(e.target.value)} />
                            </div>
                            <p style={{ color: "red" }}>{errMsg}</p>
                            <button type="submit" className="btn btn-primary">Login</button>
                        </form>
                        <a className='active' style={{ "cursor": "pointer" }} onClick={(e) => {
                            e.preventDefault()
                            navigate('/add')
                        }} >new user?click to register</a>
                    </div>
                    <div className='col sm-1'></div>
                </div>
            </div>
        </>
    )
}
