import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import Navbar from '../Components/Navbar'
import { apiBaseUrl } from '../Config'

export default function Users() {
    const [users, setUsers] = useState([])
    const [errMsg, SetErrMsg] = useState("")

    useEffect(() => {
        async function getusers() {
            try {
                let res = await axios.get(`${apiBaseUrl}user`)
                if (res.data.status === 200) {
                    SetErrMsg("")
                    setUsers(res.data.msg)
                } else {
                    SetErrMsg(res.data.msg)
                }
            } catch (err) {
                SetErrMsg("Something Wrong")
            }
        }
        getusers()
    }, [])
    return (
        <>
            <Navbar />
            <div className='container my-5' >
                <h1 className='text-center'> Users</h1>
                <Link to='/add' className='btn btn-danger mb-4'>Add user</Link>
                {
                    errMsg ? <h2 className='text-center'>{errMsg}</h2> :
                        <table className="table table-info text-center table-responsive">
                            <thead>
                                <tr >
                                    <th >S No.</th>
                                    <th >E Mail</th>
                                    <th >Added On</th>
                                </tr>
                            </thead>
                            <tbody>
                                {
                                    users.map((v, i) => {
                                        return (
                                            <tr key={i}>
                                                <th >{i+1}</th>
                                                <th >{v.email}</th>
                                                <th >{v.addedOn}</th>
                                            </tr>
                                        )
                                    })
                                }
                            </tbody>
                        </table>
                }
            </div>
        </>
    )
}





