import React from 'react'
import { useNavigate } from 'react-router-dom'
import { Link } from 'react-router-dom'

export default function Navbar() {

  const navigate=useNavigate()
  const logout=(e)=>{
    
      e.preventDefault()
      localStorage.removeItem("email")
      navigate('/')
  }

  const log={"color":"white"}

  return (
    <>
    <nav className="navbar navbar-expand-lg navbar-dark bg-dark ">
  <div className="container-fluid ">
    <Link className="nav-link active" to="/dashboard">Dashboard </Link>
    <button className="navbar-toggler " type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav me-auto mb-2 mb-lg-0">
        <li className="nav-item">
          <Link className="nav-link active " aria-current="page" to="/users">Admin-Users</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link" to="/categories">Category</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link" to="/products">Product</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link" to="/orders">Orders</Link>
        </li>
        </ul>
      <button className="btn btn-outline-success" type="submit" onClick={logout}>Logout</button>
    </div>
  </div>
</nav>
    </>
  )
}
