import React from 'react'
import { Link } from 'react-router-dom'
import Navbar from '../Components/Navbar'
import AddCat from './AddCat'
import { useState, useEffect } from 'react'
import axios from 'axios'
import { apiBaseUrl, mediaUrl } from '../Config'


export default function Category() {
    const [category, setCategory] = useState([])

    useEffect(() => {
        async function fetchData() {
            let res = await axios.get(`${apiBaseUrl}category`)
            setCategory(res.data.msg)
        }
        fetchData()
    }, [])

    async function del(id) {
        let res = await axios.delete(`${apiBaseUrl}category/del/${id}`)
        if (res.data.status === 200) {
        alert('Deleted')
        window.location.reload()
        }
    }

    return (
        <>
            <Navbar />
            <div className='container-fluid'>
                <h1 className='text-center my-5'>Category</h1>
                <Link to='/addcat' className='btn btn-warning mb-3'>Add Category</Link>
                <table className="table">
                    <thead>
                        <tr>
                            <th >S.No</th>
                            <th >Name</th>
                            <th >Added By</th>
                            <th >Added On</th>
                            <th >Picture</th>
                            <th >Description</th>
                            <th>Action</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            category.map((v, i) => {
                                return (
                                    <tr key={i}>
                                        <th >{i + 1}</th>
                                        <td>{v.name.toUpperCase()}</td>
                                        <td>{v.addedBy}</td>
                                        <td>{v.addedOn}</td>
                                        <td><img style={{ "height": "100px", "width": "150px" }} src={`${mediaUrl}category/${v.picture}`} /></td>
                                        <td>{v.description.slice(0, 15)}...</td>
                                        <td><button className='btn btn-danger' onClick={() => { del(v._id) }}>Delete</button></td>
                                    </tr>
                                )
                            })
                        }
                    </tbody>
                </table>
            </div>
        </>
    )
}
