import React, { useState } from 'react'
import Navbar from '../Components/Navbar'
import axios from 'axios'
import { apiBaseUrl } from '../Config'
import { useNavigate } from 'react-router-dom'




export default function AddCat() {
    const [name, setName] = useState('');
    const [picture, setPicture] = useState('');
    const [description, setDescription] = useState('');
    const [errMsg, setErrMsg] = useState("");
    const navigate = useNavigate();

    function chooseFile(e) {
        let myFile = e.target.files[0]
        let { name } = myFile
        let nameArr = name.split('.')
        let fileExt = nameArr[nameArr.length - 1]
        if (fileExt === "jpg" || fileExt === "png") {
            setPicture(myFile)
            setErrMsg("")
        } else {
            e.target.value = ""
            setPicture("")
            setErrMsg("please choose valid file")
        }


    }

    async function catForm(e) {
        e.preventDefault();
        if (!name || !picture || !description) {
            setErrMsg("please fill fields")
        } else {
            try {
                let formData = new FormData();
                formData.append("name", name);
                formData.append("description", description);
                formData.append("picture", picture);
                formData.append("addedBy", localStorage.getItem('email'))
                let res = await axios.post(`${apiBaseUrl}category/add`, formData)
                if (res.status === 200) {
                    setErrMsg("")
                    navigate("/categories")
                }
            } catch (err) {
                console.log(err)
                setErrMsg("Something Wrong please try again")

            }
        }
    }


    return (
        <>
            <Navbar />
            <div className='container-fluid'>
                <h1 className='text-center my-5'>Add Category</h1>
                <div className='row'>
                    <div className='col-sm-2'></div>
                    <div className='col-sm-8'>

                        <form onSubmit={catForm}>
                            <div className='form-group'>
                                <label>Name</label>
                                <input className='form-control' onChange={(e) => setName(e.target.value)} type="text" />
                            </div>
                            <div className='form-group'>
                                <label>Picture</label>
                                <input onChange={chooseFile} className='form-control' type="file" />
                            </div>
                            <div className='form-group mb-5'>
                                <label>Description</label>
                                <textarea className='form-control' onChange={(e) => setDescription(e.target.value)}></textarea>
                            </div>
                            {
                                errMsg ? <h3 className='text-danger'>{errMsg}</h3> : ""
                            }
                            <button type='submit' disabled={errMsg ? true : false} className='btn btn-success'>Add category</button>
                        </form>
                    </div>
                    <div className='col-sm-2'></div>
                </div>
            </div>
        </>
    )
}
